




















































import { Component, Prop, Watch, Emit, Vue } from 'vue-property-decorator';
import moment from 'moment';

@Component
export default class CAge extends Vue {
  // Data
  private dataNowMoment: any = moment(new Date());
  private dataBirthdayPoint: {
    year: number,
    month: number,
    day: number,
    hour: number,
    minute: number,
    second: number,
  } = {
    year: 0,
    month: 0,
    day: 0,
    hour: 0,
    minute: 0,
    second: 0,
  };
  private dataPointDiff: {
    total: string,
    year: number,
    month: number,
    day: number,
    hour: number,
    minute: number,
    second: number,
  } = {
    total: '',
    year: 0,
    month: 0,
    day: 0,
    hour: 0,
    minute: 0,
    second: 0,
  };
  private dataYears: number[] = [];
  private dataMonths: number[] = [];
  private dataDays: number[] = [];
  private dataHours: number[] = [];
  private dataMinutesAndSeconds: number[] = [];
  private dataAlert: string = '';

  // Watch
  @Watch('dataBirthdayPoint')
  onEventPointChange(newValue: string, oldValue: string): void {
  }
  @Watch('dataPointDiff')
  onPointDiffChange(newValue: string, oldValue: string): void {
  }
  @Watch('dataNowMoment')
  onNowMomentChange(newValue: string, oldValue: string): void {
    this.diffDatetime();
  }

  // Lifecycle hook
  created(): void {
    let birthdayPoint: any = this.dataNowMoment.clone().startOf('year');
    this.formatPoint(birthdayPoint);
    this.initYears();
    this.initMonths();
    this.updateDays(birthdayPoint, this.dataDays);
    this.initHours();
    this.initMinutesAndSeconds();
    this.diffDatetime();
    this.getNow();
  }

  // Methods
  private getNow(): void {
    setInterval((): void => {
      let now: any = moment(new Date());
      if (this.dataNowMoment !== now) {
        this.dataNowMoment = now;
      }
    }, 200);
  }
  private formatPoint(birthdayPoint: any): void {
    this.dataBirthdayPoint = {
      year: birthdayPoint.year(),
      month: birthdayPoint.month(),
      day: birthdayPoint.date(),
      hour: birthdayPoint.hour(),
      minute: birthdayPoint.minutes(),
      second: birthdayPoint.second(),
    };
  }
  private initYears(): void {
    const YEAR_TERM: number = 120;
    let startPointYear: number;
    let endPointYear: number;
    let nowMoment: any = this.dataNowMoment.clone();
    startPointYear = nowMoment.clone().subtract(YEAR_TERM, 'years').year();
    endPointYear = nowMoment.year();
    while (startPointYear <= endPointYear) {
      this.dataYears.push(endPointYear--);
    }
  }
  private initMonths(): void {
    const MONTH_TERM: number = 12;
    for (let i: number = 0; i < MONTH_TERM; i++) {
      this.dataMonths.push(i);
    }
  }
  private calcNaturalMonth(month: number): number {
    return month + 1;
  }
  private updateDays(datetime: any, days: number[]): void {
    let endPointDay: number = datetime.clone().endOf('month').date();
    if (days.length < endPointDay) {
      for (let i: number = days.length + 1; i <= endPointDay; i++) {
        days.push(i);
      }
    } else if (endPointDay < days.length) {
      for (let i: number = days.length; endPointDay < i; i--) {
        days.pop();
      }
    }

  }
  private initHours(): void {
    const HOUR_TERM: number = 24;
    for (let i: number = 0; i < HOUR_TERM; i++) {
      this.dataHours.push(i);
    }
  }
  private initMinutesAndSeconds(): void {
    const MINUTE_AND_SECOND_TERM: number = 60;
    for (let i: number = 0; i < MINUTE_AND_SECOND_TERM; i++) {
      this.dataMinutesAndSeconds.push(i);
    }
  }
  private toMoment(point: {year: number, month: number, day: number, hour: number, minute: number, second: number}): any {
    let startOfMonth: any = moment(new Date(point.year, point.month, 1));
    let endDayOfMonth: number = startOfMonth.endOf('month').date();
    if (endDayOfMonth < point.day) {
      point.day = endDayOfMonth;
    }
    return moment(new Date(
      point.year,
      point.month,
      point.day,
      point.hour,
      point.minute,
      point.second,
    ));
  }
  private diffDatetime(): void {
    let nowMoment: any = this.dataNowMoment.clone();
    let birthdayMoment: any = this.toMoment(this.dataBirthdayPoint);
    if (0 < birthdayMoment.diff(nowMoment)) {
      this.dataAlert = '誕生日は現在よりも前の時刻を設定してください。';
    } else {
      // Alert
      this.dataAlert = '';
      // Each diff
      Vue.set(this.dataPointDiff, 'year', nowMoment.diff(birthdayMoment, 'years'));
      Vue.set(this.dataPointDiff, 'month', nowMoment.diff(birthdayMoment, 'months'));
      Vue.set(this.dataPointDiff, 'day', nowMoment.diff(birthdayMoment, 'days'));
      Vue.set(this.dataPointDiff, 'hour', nowMoment.diff(birthdayMoment, 'hours'));
      Vue.set(this.dataPointDiff, 'minute', nowMoment.diff(birthdayMoment, 'minutes'));
      Vue.set(this.dataPointDiff, 'second', nowMoment.diff(birthdayMoment, 'seconds'));
      // Total diff
      let pointDiffTotal: string = '';
      let yearDiff: number = nowMoment.diff(birthdayMoment, 'years');
      pointDiffTotal += String(yearDiff) + '歳 ';
      birthdayMoment.add(yearDiff, 'years');
      let monthDiff: number = nowMoment.diff(birthdayMoment, 'months');
      pointDiffTotal += String(monthDiff) + 'ヵ月 ';
      birthdayMoment.add(monthDiff, 'months');
      let dayDiff: number = nowMoment.diff(birthdayMoment, 'days');
      pointDiffTotal += String(dayDiff) + '日 ';
      birthdayMoment.add(dayDiff, 'days');
      let hourDiff: number = nowMoment.diff(birthdayMoment, 'hours');
      pointDiffTotal += String(hourDiff) + '時間 ';
      birthdayMoment.add(hourDiff, 'hours');
      let minuteDiff: number = nowMoment.diff(birthdayMoment, 'minutes');
      pointDiffTotal += String(minuteDiff) + '分 ';
      birthdayMoment.add(minuteDiff, 'minutes');
      let secondDiff: number = nowMoment.diff(birthdayMoment, 'seconds');
      pointDiffTotal += String(secondDiff) + '秒 ';
      Vue.set(this.dataPointDiff, 'total', pointDiffTotal);
    }
  }
  private listenEventPointYearChange($event: Event): void {
    this.dataBirthdayPoint.year = Number((<HTMLInputElement>(<any>$event).target).value);
    let datetime: any = this.toMoment(this.dataBirthdayPoint);
    this.updateDays(datetime, this.dataDays);
    this.diffDatetime();
  }
  private listenEventPointMonthChange($event: Event): void {
    this.dataBirthdayPoint.month = Number((<HTMLInputElement>(<any>$event).target).value);
    let datetime: any = this.toMoment(this.dataBirthdayPoint);
    this.updateDays(datetime, this.dataDays);
    this.diffDatetime();
  }
  private listenEventPointDayChange($event: Event): void {
    this.dataBirthdayPoint.day = Number((<HTMLInputElement>(<any>$event).target).value);
    this.diffDatetime();
  }
  private listenEventPointHourChange($event: Event): void {
    this.dataBirthdayPoint.hour = Number((<HTMLInputElement>(<any>$event).target).value);
    this.diffDatetime();
  }
  private listenEventPointMinuteChange($event: Event): void {
    this.dataBirthdayPoint.minute = Number((<HTMLInputElement>(<any>$event).target).value);
    this.diffDatetime();
  }
  private listenEventPointSecondChange($event: Event): void {
    this.dataBirthdayPoint.second = Number((<HTMLInputElement>(<any>$event).target).value);
    this.diffDatetime();
  }
}
